footer {
  padding-top: 4rem;
  padding-bottom: 4rem; }
  footer h2 {
    line-height: 5.25rem; }
  footer ul {
    line-height: 2.0625rem; }
    footer ul a {
      color: white;
      text-decoration: none; }
      footer ul a:hover {
        color: #891818; }

.info--banner {
  min-height: 100vh;
  background-image: url("../images/banner.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 101% 101%; }

.info__cnt__title {
  line-height: 6.625rem; }
  .info__cnt__title span {
    line-height: 2.625rem; }

.info__cnt__iconLinks li a {
  display: inline-block; }
  .info__cnt__iconLinks li a:hover {
    opacity: 0.5; }

.about {
  padding-top: 3.5rem;
  padding-bottom: 3.5rem; }
  .about__cnt__title {
    line-height: 5.25rem; }
  .about__cnt__exps {
    width: 24rem;
    border-top: 1px solid black;
    line-height: 2rem; }
    .about__cnt__exps li {
      display: flex;
      align-items: center;
      justify-content: center; }
      .about__cnt__exps li::before {
        content: '';
        width: 1.375rem;
        height: 1.375rem;
        border-radius: 50%;
        background: #891818;
        margin-right: 1rem; }

.brand {
  padding-top: 3.5rem;
  padding-bottom: 10rem; }
  .brand__cnt__title {
    line-height: 5.25rem; }
  .brand__cnt h3 + p {
    margin-bottom: 4.25rem; }
  .brand__cnt__brands {
    gap: 2.5rem 1.5rem; }

.work {
  padding-top: 3.5rem;
  padding-bottom: 9rem; }
  .work__cnt__title {
    line-height: 5.25rem; }
  .work__cnt__works {
    gap: 3rem 1.5rem; }
    .work__cnt__works div {
      overflow: hidden; }
      .work__cnt__works div img {
        transition: all 0.2s linear; }
        .work__cnt__works div img:hover {
          cursor: pointer;
          transform: scale(1.1); }
      .work__cnt__works div:nth-of-type(1) {
        margin-bottom: 1rem; }
    .work__cnt__works p {
      line-height: 2.625rem; }
